<template>
  <div class="mainTableContainer">
    <div class="table-nav">
      <el-link>xx </el-link>
      <el-link type="primary"></el-link>
      <el-link type="primary"></el-link>
      <el-link type="primary"></el-link>
      <el-link type="primary"></el-link>
      <el-link type="primary"></el-link>
    </div>
    <el-table
      :data="mainTable"
      style="width: 100%"
      :span-method="objectSpanMethod"
      border
      highlight-current-row
      class="tb-edit"
      :row-class-name="rowClass"
    >
      <el-table-column label="       " align="center">
        <el-table-column label-class-name="font12" label="" header-align="center">
          <el-table-column
            header-align="left"
            label-class-name="font10 color999"
            label="    "
          >
            <el-table-column label="     " header-align="center">
              <el-table-column width="40px" align="center">
                <template slot-scope="scope">
                  <div
                    style="width: 14px;font-size:14px;word-wrap:break-word;letter-spacing:20px;"
                  >{{ scope.row.itemType }}</div>
                </template>
              </el-table-column>

              <el-table-column>
                <template slot-scope="scope">
                  <el-popover
                    v-if="scope.row.titles&&scope.row.titles.length>0"
                    trigger="hover"
                    placement="right"
                  >
                    <div>
                      <p v-for="title in  scope.row.titles" :key="title">{{title}}</p>
                    </div>
                    <div slot="reference">
                      <el-badge is-dot class="hasTitle">{{ scope.row.item }}</el-badge>
                    </div>
                  </el-popover>
                  <div v-else style="white-space:pre;">{{ scope.row.item }}</div>
                </template>
              </el-table-column>
            </el-table-column>
            <el-table-column label="" header-align="center" prop="lc"></el-table-column>
            <el-table-column label="" header-align="center">
              <el-table-column label="" header-align="center">
                <template scope="scope">
                  <el-input
                    size="small"
                    :disabled="isDisable(scope.row)"
                    :change="inputChange(scope.row,scope.row.a1)"
                    v-model="scope.row.a1"
                    placeholder=""
                  ></el-input>
                  <span>{{scope.row.a1}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" header-align="center">
                <template scope="scope">
                  <el-input
                    size="small"
                    :disabled="isDisable(scope.row)"
                    :change="inputChange(scope.row,scope.row.a1)"
                    v-model="scope.row.a2"
                    placeholder=""
                  ></el-input>
                  <span>{{scope.row.a2}}</span>
                </template>
              </el-table-column>
            </el-table-column>
            <el-table-column label="" header-align="center">
              <el-table-column label="" header-align="center">
                <template scope="scope">
                  <el-input
                    size="small"
                    :disabled="isDisable(scope.row)"
                    :change="inputChange(scope.row,scope.row.a1)"
                    v-model="scope.row.b1"
                    placeholder=""
                  ></el-input>
                  <span>{{scope.row.b1}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" header-align="center">
                <template scope="scope">
                  <el-input
                    size="small"
                    :disabled="isDisable(scope.row)"
                    :change="inputChange(scope.row,scope.row.a1)"
                    v-model="scope.row.b2"
                    placeholder=""
                  ></el-input>
                  <span>{{scope.row.b2}}</span>
                </template>
              </el-table-column>
            </el-table-column>
          </el-table-column>
        </el-table-column>
      </el-table-column>
    </el-table>
    <p class="can-space"> </p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mainTable: [
        {
          lc: 1,
          itemType: "",
          item: "",
          titles: [
            `1 `,
            ``,
            ` `,
            ``,
            ``,
            ``,
            `231`
          ]
        },
        {
          lc: 2,
          item: "",
          titles: [
            ``,
            ``
          ]
        },
        {
          lc: 3,
          item: "           "
        },
        {
          lc: 4,
          item: "           ",
          titles: [
            ``,
            ``,
            ``,
            ``,
            ``
          ]
        },
        {
          lc: 5,
          item: "",
          titles: [
            ``,
            ``,
            ``,
            ``,
            ``
          ]
        },
        {
          lc: 6,
          item: "",
          titles: [
            ``,
            ``,
            ``,
            ``
          ]
        },
        {
          lc: 7,
          item: "",
          titles: [
            ``
          ]
        },
        {
          lc: 8,
          item: "",
          titles: [
            ``,
            ``,
            `9108`
          ]
        },
        {
          lc: 9,
          item: ""
        },
        {
          lc: 10,
          item: "           "
        },
        {
          lc: 11,
          itemType: "",
          item: "",
          titles: [
            ``,
            ``,
            ``
          ]
        },
        {
          lc: 12,
          item: "",
          titles: [
            `+12`
          ]
        },
        {
          lc: 13,
          item: "",
          titles: [
            ``,
            `20`,
            `20 `,
            `0`
          ]
        },
        {
          lc: 14,
          item: ""
        },
        {
          lc: 15,
          item: "",
          titles: [
            ``
          ]
        },
        {
          lc: 16,
          item: ""
        },
        {
          lc: "17=12+13-14-15+16",
          item: ""
        },
        {
          lc: `1817<111711`,
          item: "",
          titles: [
            ``,
            ``,
            ` `,
            ``,
            `0 `
          ]
        },
        {
          lc: `19=11-18`,
          item: ""
        },
        {
          lc: `20=17-18`,
          item: ""
        },
        {
          lc: 21,
          item: "",
          titles: [
            ``,
            ``,
            `=`
          ]
        },
        {
          lc: 22,
          item: ""
        },
        {
          lc: 23,
          item: "",
          titles: [
            ``,
            ``,
            `1921`,
            `19211921`
          ]
        },
        {
          lc: `24=19+21-23`,
          item: ""
        },
        {
          lc: 25,
          itemType: "",
          item: "",
          titles: [
            `32`,
            ` 32`
          ]
        },
        { lc: 26, item: "" },
        {
          lc: `27=28+29+30+31`,
          item: "",
          titles: [
            ``
          ]
        },
        {
          lc: 28,
          item: "",
          titles: [
            ``,
            ``,
            `;`,
            `;`,
            ``
          ]
        },
        { lc: 29, item: "" },
        { lc: 30, item: "" },
        {
          lc: 31,
          item: "",
          titles: [
            ``
          ]
        },
        {
          lc: `32=24+25+26-27`,
          item: "",
          titles: [
            ``
          ]
        },
        {
          lc: `33=25+26-27`,
          item: "0",
          titles: [
            ``
          ]
        },
        {
          lc: `3424-28-29`,
          item: "()",
          titles: [
            ``
          ]
        },
        { lc: 35, item: "" },
        {
          lc: 36,
          item: "",
          titles: [
            `3838`
          ]
        },
        {
          lc: 37,
          item: "",
          titles: [
            ` `
          ]
        },
        {
          lc: `38=16+22+36-37`,
          item: "",
          titles: [
            ``
          ]
        },
        {
          itemType: `    
    
    
    
            `,
          item: "      ",
          a2: `
                               
                               
                               
                               
                                       `,
          b1: ""
        },
        {
          item:
            "                                                                      ",
          b1:
            "   "
        },
        {
          item:
            "                                             ",
          b1: ""
        },
        { item: "", b1: "" },
        { item: "", b1: "" },
        {
          item:
            "                                                                                ",
          b1:
            "                                                                           "
        }
      ]
    };
  },
  methods: {
    inputChange(row, value) {
      let lc = parseInt(row.lc);
      let map = [
        {
          lc: "17",
          lcs: [12, 13, 14, 15, 16],
          handel: function(lc, v) {
            if (lc == 14 || lc == 15) {
              return -v;
            }
            return v;
          }
        },
        {
          lc: "18",
          lcs: [17, 11],
          handel: function(lc, v, total) {
            debugger;
            if (lc == 11) return v;
            if (lc == 17 && v < total) {
              return v - total;
            }
            return 0;
          }
        },
        {
          lc: "19",
          lcs: [11, 18],
          handel: function(lc, v) {
            if (lc == 18) return -v;
            return v;
          }
        },
        {
          lc: "20",
          lcs: [17, 18],
          handel: function(lc, v) {
            if (lc == 18) return -v;
            return v;
          }
        },
        {
          lc: "24",
          lcs: [19, 21, 23],
          handel: function(lc, v) {
            if (lc == 23) return -v;
            return v;
          }
        },
        {
          lc: "27",
          lcs: [28, 29, 30, 31],
          handel: function(lc, v) {
            return v;
          }
        },
        {
          lc: "32",
          lcs: [24, 25, 26, 27],
          handel: function(lc, v) {
            if (lc == 27) return -v;
            return v;
          }
        },
        {
          lc: "33",
          lcs: [25, 26, 27],
          handel: function(lc, v) {
            if (lc == 27) return -v;
            return v;
          }
        },
        {
          lc: "34",
          lcs: [24, 28, 29],
          handel: function(lc, v) {
            if (lc == 24) return v;
            return -v;
          }
        },
        {
          lc: "38",
          lcs: [16, 22, 36, 37],
          handel: function(lc, v) {
            if (lc == 37) return -v;
            return v;
          }
        }
      ];
      map.forEach(t => {
        const { lc, lcs, handel } = t;

        let toUpdate = this.mainTable.filter(m => parseInt(m.lc) == lc)[0];
        let toCompute = this.mainTable.filter(m =>
          lcs.includes(parseInt(m.lc))
        );
        let result = { a1: 0, a2: 0, b1: 0, b2: 0 };

        toCompute.forEach(m => {
          for (const ab in result) {
            if (result.hasOwnProperty(ab) && m.hasOwnProperty(ab)) {
              let v = parseInt(m[ab]);
              if (!isNaN(v)) {
                result[ab] += handel(parseInt(m.lc), v, result[ab]);
              }
            }
          }
        });

        toUpdate.a1 = result.a1;
        toUpdate.a2 = result.a2;
        toUpdate.b1 = result.b1;
        toUpdate.b2 = result.b2;
      });
    },
    isDisable(row) {
      let lc = parseInt(row.lc);
      let lcs = [17, 18, 19, 20, 24, 27, 32, 33, 34, 38];
      if (lcs.includes(lc)) {
        return true;
      }
      return false;
    },
    rowClass(row, rowIndex) {
      if (row.rowIndex >= 38) {
        return "non-edit can-space";
      }
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex == 0) {
        if (rowIndex == 0) {
          return {
            rowspan: 10,
            colspan: 1
          };
        } else if (rowIndex == 10) {
          return {
            rowspan: 14,
            colspan: 1
          };
        } else if (rowIndex == 24) {
          return {
            rowspan: 14,
            colspan: 1
          };
        } else if (rowIndex == 38) {
          return {
            rowspan: 6,
            colspan: 2
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }

      if (rowIndex == 38 && columnIndex == 4) {
        return {
          rowspan: 6,
          colspan: 1
        };
      }

      switch (rowIndex) {
        case 38:
        case 39:
        case 40:
        case 41:
        case 42:
        case 43:
          if (columnIndex == 1 || columnIndex == 5) {
            return {
              rowspan: 1,
              colspan: 2
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
      }
    }
  }
};
</script>
<style type="css" >
.font12 {
  font-size: 12px;
}
.font10 {
  font-size: 10px;
  color: #999;
}
.el-table__footer-wrapper thead div,
.el-table__header-wrapper thead div.color999 {
  color: #999;
}
.hasTitle {
  margin-top: 10px;
  margin-right: 40px;
}

.tb-edit .el-input {
  display: none;
}
.tb-edit .el-select {
  display: none;
}
.tb-edit .current-row:not(.non-edit) .el-input {
  display: block;
}
.tb-edit .current-row:not(.non-edit) .el-select {
  display: block;
}
.tb-edit .current-row:not(.non-edit) .el-input + span {
  display: none;
}
.tb-edit .current-row:not(.non-edit) .el-select + span {
  display: none;
}
.el-table .can-space .cell {
  white-space: pre;
}
.can-space {
  white-space: pre;
}
.table-nav {
  margin: 15px;
}
</style>



